.chat-creation {
	background-color: rgb(255, 255, 204);
	border: none;
	border-radius: 20px;
	margin-right: 10%;
	margin-bottom: 4px;
	position: sticky;
	bottom: 0;
	float: right;
	align-self: flex-end;
	display: flex;
	flex-direction: row;
	justify-items: flex-end;
}

button.chat-create {
	background-color: rgb(255, 255, 204);
	border: none;
	border-radius: 0 40% 40% 0;
	height: 50px;
	width: 50px;
	font-size: 18pt;
}

button.chat-create:hover {
	transition: background-color 1s ease 0s;
	transition: font-size 1s ease 0s;
	animation: blink 4s infinite;
}

@keyframes blink {
	0% {
		background-color: rgb(255, 255, 204);
		font-size: 18pt;
	}
	50% {
		background-color: rgb(255, 255, 0);
		font-size: 22pt;
	}
	100% {
		background-color: rgb(255, 255, 204);
		font-size: 18pt;
	}
}

.chat-creation-form {
	height: 50px;
}

.chat-name-input {
	border: 0;
	border-radius: 20px;
	outline: none;
	width: calc(100% - 2px);
	height: 100%;
	background-color: rgb(255, 255, 204);
}
