.chat-top-area {
	font: 12pt 700;
	border: 1px solid rgb(0, 0, 0);
	border-radius: 20px;
	background-color: rgb(0, 0, 255);
	width: 100%;
	height: calc(100% - 6px);
	margin-top: 2px;
	text-align: center;
	display: flex;
	flex-direction: row;
}

.chat-top-name {
	color: rgb(255, 255, 255);
	margin-left: 4px;
}

div button.chat-bars-button {
	background-color: rgb(0, 0, 255);
	border: none;
	border-radius: 20px;
	height: 100%;
	width: 60px;
	font-size: 16pt;
	margin-left: 4px;
}
