div.chat-list-area {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}

div.wrap-chat-list {
	/* necessary for scrolling */
	height: 85%;
	min-height: 0;
	overflow: auto;
	scroll-behavior: smooth;
}

.chat-list-head {
	/* note: find a way to actually attach it to the top */
	vertical-align: top;
	width: 100%;
	height: 15%;
}

chat-list {
	height: 100%;
	width: 100%;
	vertical-align: top;
}

.chats-area {
	font: 12pt 700;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	clear: none;
}
